import React, { useState, useEffect } from "react";
import { Card, Button, Form } from "react-bootstrap";

function Survey() {
  const [featureOneValue, setFeatureOneValue] = useState("");
  const [featureTwoValue, setFeatureTwoValue] = useState("");
  const [featureThreeValue, setFeatureThreeValue] = useState("");
  const [featureFourValue, setFeatureFourValue] = useState("");
  const [featureFiveValue, setFeatureFiveValue] = useState("");

  const [inputValue, setInputValue] = useState({
    f1: "",
    f2: "",
    f3: "",
    f4: "",
    f5: "",
  });

  function handleEnter(event) {
    if (event.keyCode === 13) {
      const form = event.target.form;
      const index = Array.prototype.indexOf.call(form, event.target);
      form.elements[index + 1].focus();
      event.preventDefault();
    }
  }

  const handleSubmitFeatures = () => {
    setInputValue({
      f1: featureOneValue,
      f2: featureTwoValue,
      f3: featureThreeValue,
      f4: featureFourValue,
      f5: featureFiveValue,
    });
  };

  return (
    <>
      <Card className="mt-4">
        <Card.Body>
          <h2 className="text-center mb-4">Заполните данные о пациенте</h2>
          <Form>
            <Form.Group id="featureOne">
              <Form.Label>Feature 1</Form.Label>
              <Form.Control
                type="text"
                autoFocus={true}
                onKeyDown={handleEnter}
                onChange={(event) => setFeatureOneValue(event.target.value)}
                value={featureOneValue}
                autoComplete="false"
              />
            </Form.Group>
            <Form.Group id="featureTwo">
              <Form.Label>Feature 2</Form.Label>
              <Form.Control
                type="text"
                onKeyDown={handleEnter}
                onChange={(event) => setFeatureTwoValue(event.target.value)}
                value={featureTwoValue}
                autoComplete="false"
              />
            </Form.Group>
            <Form.Group id="featureThree">
              <Form.Label>Feature 3</Form.Label>
              <Form.Control
                type="text"
                onKeyDown={handleEnter}
                onChange={(event) => setFeatureThreeValue(event.target.value)}
                value={featureThreeValue}
                autoComplete="false"
              />
            </Form.Group>
            <Form.Group id="featureFour">
              <Form.Label>Feature 4</Form.Label>
              <Form.Control
                type="text"
                onKeyDown={handleEnter}
                onChange={(event) => setFeatureFourValue(event.target.value)}
                value={featureFourValue}
                autoComplete="false"
              />
            </Form.Group>
            <Form.Group id="featureFive">
              <Form.Label>Feature 5</Form.Label>
              <Form.Control
                type="text"
                onChange={(event) => setFeatureFiveValue(event.target.value)}
                value={featureFiveValue}
                autoComplete="false"
              />
            </Form.Group>
            {/* <Form.Group id="featureSix">
              <Form.Label>Feature 6</Form.Label>
              <Form.Control type="input" ref={featureSixRef} />
            </Form.Group>
            <Form.Group id="featureSeven">
              <Form.Label>Feature 7</Form.Label>
              <Form.Control type="input" ref={featureSevenRef} />
            </Form.Group> */}
          </Form>
        </Card.Body>
      </Card>
    </>
  );
}

export default Survey;
